/*CSS Reset from http://meyerweb.com/eric/tools/css/reset/*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
	box-sizing : border-box;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

html {
	height : 100%;
}
/* Body typeface and color */
body {
	font-family : "CamphorW04-Medium", sans-serif;
	background-color : #f8f8f8;
	height : 100%;
}

/* Remove default styling for links */
a, a:active, a:visited {
	color : #b9b9b9;
	text-decoration : none;
}

/* Navigation */
.topnav {
	position : fixed;
	top: 0;
	left : 0;
	background-color: #ffffff;
	width : 100%;
	height : 50px;
}

.topnav__list {
	padding : 20px;
	display : flex;
	justify-content: space-between;
}

.topnav__sublist {
	width : 40%;
	display : flex;
	justify-content : space-between;
}

/*Container div for sidebar and wrapper*/
.reviews-sidebar-wrapper {
	margin-top : 50px;
	display : flex;
	height : 100%;
}

/*Reviews section*/
.reviews {
	width : 73%;
}

/* Review Card */
.review__card {
	background-color : #ffffff;
	margin: 6em 5em 5em 5em;
	display : flex;
	border-radius : 5px;
	min-height : 40%;
}

.review__card__contentBox {
	width : 75%;
	display : flex;
	flex-direction : column;
}

.review__card__text {
	padding : 2em 1.5em 2em 4.5em;
	flex : 2;
}

.review__heading {
	font-family : "CamphorW01-Medium";
	font-size : 0.9rem;
	margin-bottom : 1em;

}

.review__paragraph {
	line-height : 1.4;
	font-size : 0.8rem;
}

/*Review Tags*/
.review__card__taglist {
	display : flex;
}

.review__card__footer {
	font-size : 0.8rem;
	border-top: 1px solid #ededed;
	flex : 1;
}

.review__card__bookCover {
	width : 25%;
	background-color : #F36A6F;
	border-top-right-radius: 5px;
	border-bottom-right-radius: 5px;
}

/* Sidebar */
.sidebar {
	width : 27%;
	height : 100%;
	background-color : #ffffff;
	display : flex;
	flex-direction : column;
	justify-content: space-around;
}

.sidebar__heading__tags, .sidebar__heading__recentlyReviewed {
	font-family : "CamphorW01-Medium";
}
